* {
  font-family: "Lato", sans-serif;
}

#content {
  width: 100%;
  height: 100%;
  padding-right: 200px;
}

#menu li {
  display: inline;
}

#menu a:link,
#menu a:visited {
  text-decoration: none;
  color: #f2977d;
  font-weight: 300;
  font-size: 24px;
}

#menu :not(:last-child) {
  border-right: 2.5px solid #f2977d;
  padding-right: 15px;
  padding-left: 15px;
}

#menu li:last-child {
  padding-left: 15px;
  padding-right: 15px;
}

#container-logo {
  width: 139px;
}

#container-nav {
  width: content;
}

// #container-nav {
//   right: 10px;
//   position: absolute;
//   right: 200px;
// }

header {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

#content {
  padding-left: 150px;
}

#photo-equipe {
  margin-top: -70px;
}

.gros-titre {
  font-size: 40px;
  font-weight: 900;
  text-transform: uppercase;
  text-align: center;
}

#photo {
  width: 100%;
  border-radius: 41px;
  padding-top: 30px;
  padding-bottom: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#photo {
  background-color: rgba(22, 22, 22, 0.05);
}

#top {
  display: flex;
  align-items: center;
  gap: 50px;
  width: 90%;
}

#texte-presentation {
  display: flex;
  border: solid #f2977d;
  width: auto;
  height: auto;
  border-style: dashed;
  border-width: 5px;
}

#description-presentation {
  padding: 50px;
  word-break: break-word;
}
