    background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' stroke='black' stroke-width='5' stroke-dasharray='400 220' stroke-dashoffset='0' stroke-linecap='square'/%3e%3c/svg%3e"); 
    
    **/

#texte-presentation > p {
  width: 100%;
  align-self: center;
  text-align: center;
}

#contenu {
  font-size: 20px;
  font-weight: 400;
  width: 100%;
  text-align: justify;
}

#contenu > p {
  margin-left: 70px;
  margin-right: 70px;
  line-height: 36px;
}

#nom {
  font-size: 40px;
  font-weight: 500;
}

#metier {
  font-size: 30px;
  font-weight: 200;
}

@media all and (max-width: 990px) {
  header {
    margin-bottom: 200px;
  }
  #top {
    flex-direction: column;
  }
  #content {
    padding-left: 0%;
    padding-right: 0%;
  }
  #photo {
    padding: 20px;
  }
  #contenu > p {
    margin-left: 10px;
    margin-right: 10px;
  }
  #texte-presentation {
    width: auto;
  }
}
